.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-Footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-headers {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.flash1 {
  color: rgb(173, 255, 244);
}
.flash2 {
  color: rgb(255, 210, 173);
}
.flash3 {
  color: rgb(255, 173, 173);
}
a:link {
  text-decoration: none;
}

  /* visited link */
  a:visited {
    text-decoration: none;
  
  }

  /* mouse over link */
  a:hover {
    color: rgb(185, 255, 195);
    text-decoration: none;
  
  }

  /* selected link */
  a:active {
    color: rgb(80, 80, 80);
  }

.App-link {
  color: #61dafb;
}